* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: #fff3ec;
  text-decoration: none;
}

ul {
  list-style: none;
}

.flex {
  display: flex;
}

.header {
  z-index: 10;
  width: 100%;
  background-image: linear-gradient(#340c0c, #5c302f);
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  position: fixed;
}

.menu {
  justify-content: space-between;
}

.list {
  list-style: none;
}

.list__item {
  color: #fff3ec;
  color: #fff3ec;
  text-align: center;
  margin-top: 3px;
  margin-left: 40px;
  font-family: Outfit, sans-serif;
  font-weight: lighter;
}

.list__item:hover {
  cursor: pointer;
  color: #eec9af;
  font-weight: normal;
  transform: scale(1.1);
}

#logo {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 25px;
}

.icons__item {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 25px;
  -o-object-fit: cover;
  object-fit: cover;
  margin-right: 30px;
}

.icons__item:hover {
  transform: scale(1.2);
}

.icons__item--menu {
  display: none;
}

.container {
  margin: 0 50px;
  position: relative;
  top: 110px;
}

.container__title {
  color: #340c0c;
  margin-bottom: 20px;
  font-family: Marcellus, serif;
  font-size: 1.8em;
}

.bag {
  background-color: #fff3ec;
  margin-bottom: 30px;
  padding: 30px;
}

.bag li {
  height: 250px;
  background-color: #fff;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
  position: relative;
}

.bag__infosection {
  width: 30%;
  flex-direction: column;
  margin: 60px 20px;
  display: flex;
}

.bag__name {
  color: #340c0c;
  font-family: Marcellus, serif;
