  color: #340c0c;
  margin-top: 6px;
  font-family: Outfit, sans-serif;
  font-weight: lighter;
}

.bag__btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  color: #340c0c;
  text-align: center;
  background: none;
  border: none;
  font-family: Outfit, sans-serif;
  font-size: 1.5em;
  font-weight: normal;
  position: absolute;
  top: 20px;
  right: 20px;
}

.total {
  color: #340c0c;
  border-top: 2px solid #340c0c;
  padding: 30px;
  font-family: Outfit, sans-serif;
